@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
    }
}

.navegacion {

    @include m.tablet{
        display: flex;
        align-items: center;
        font-size: 1.8rem;
    }

    a {
        color: #ffffff;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        &:hover {
            color: v.$verde;
        }

        @include m.tablet {
            margin-right: 2rem;

            &:last-of-type {
                margin-right: 0;
            }
        }
    }
}